@import '../../../scss/media_mixin';

.burger {
  max-width: 34px;
  width: 34px;
  height: 14px;
  position: relative;
  margin-left: 35px;
  cursor: pointer;
  display: none;

  @include _1040() {
    display: block;
  }

  &.active {
    .a {
      right: 21px;
    }

    .b {
      width: 26px;
      right: 13px;
    }

    .c {
      right: 21px;
    }
  }

  &:after {
    content: '';
    position: absolute;
    z-index: 0;
    width: 60px;
    height: 50px;
    top: 50%;
    left: 50%;
    margin: -25px 0 0 -43px;
  }

  .a {
    background-color: #333;
    display: block;
    position: absolute;
    height: 2px;
    transition: all .3s ease;
    right: 0;
    z-index: 5;
    top: 0;
    width: 26px;
    cursor: pointer;
  }

  .b {
    top: 6px;
    width: 34px;
    background-color: #333;
    display: block;
    position: absolute;
    right: 0;
    height: 2px;
    transition: all .3s ease;
    z-index: 5;
    cursor: pointer;
  }

  .c {
    top: 12px;
    width: 26px;
    background-color: #333;
    display: block;
    position: absolute;
    right: 0;
    height: 2px;
    transition: all .3s ease;
    z-index: 5;
    cursor: pointer;
  }
}